import React from 'react';
import html2canvas from 'html2canvas';
import JsPDF from 'jspdf';

function FioTable({ tableData, setTableData, setShowTable, certificateRef, textBlocks, setTextBlocks }) {

    const addRow = () => {
        if (tableData.length < 10) {
            setTableData([...tableData, { lastName: '', firstName: '', middleName: '' }]);
        }
    };

    const updateRow = (index, updatedRow) => {
        const updatedData = [...tableData];
        updatedData[index] = updatedRow;
        setTableData(updatedData);
    };

    const deleteRow = (index) => {
        const updatedData = [...tableData];
        updatedData.splice(index, 1);
        setTableData(updatedData);
    };

    const handleSaveAllPDFs = async () => {
        const scale = 3;
        const pdf = new JsPDF();

        for (let rowIndex = 0; rowIndex < tableData.length; rowIndex + 1) {
            const row = tableData[rowIndex];
            if (row) {
                const fio = `${row.lastName} ${row.firstName} ${row.middleName}`;

                //   textBlocks      %   
                const updatedTextBlocks = textBlocks.map(block => ({
                        ...block,
                        text: block.text === '%' ? fio : block.text,
                    }));

                //   textBlocks   
                setTextBlocks(updatedTextBlocks);

                //    
                // eslint-disable-next-line no-await-in-loop,no-promise-executor-return
                await new Promise(resolve => setTimeout(resolve, 1000));

                //   canvas   
                // eslint-disable-next-line no-await-in-loop
                const tempCanvas = await html2canvas(certificateRef.current, { scale });

                //  base64  
                const imgData = tempCanvas.toDataURL('image/png').split(',')[1];

                //     textBlocks  PDF
                if (rowIndex !== 0) {
                    pdf.addPage();
                }
                pdf.addImage(imgData, 'PNG', 0, 0, 210, 300, '', 'FAST');

                //    textBlocks
                setTextBlocks(updatedTextBlocks);
            }
        }

        //   PDF 
        pdf.save('certificates.pdf');
    };

    return (
        <div className="table-container">
            <table className="fio-table">
                <thead>
                <tr>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                </tr>
                </thead>
                <tbody>
                {tableData.map((row, rowIndex) => (
                    <tr key={rowIndex}>
                        <td>
                            <input
                                type="text"
                                value={row.lastName}
                                onChange={(e) =>
                                    updateRow(rowIndex, {
                                        ...row,
                                        lastName: e.target.value,
                                    })
                                }
                            />
                        </td>
                        <td>
                            <input
                                type="text"
                                value={row.firstName}
                                onChange={(e) =>
                                    updateRow(rowIndex, {
                                        ...row,
                                        firstName: e.target.value,
                                    })
                                }
                            />
                        </td>
                        <td>
                            <input
                                type="text"
                                value={row.middleName}
                                onChange={(e) =>
                                    updateRow(rowIndex, {
                                        ...row,
                                        middleName: e.target.value,
                                    })
                                }
                            />
                        </td>
                        <td>
                            <button onClick={() => deleteRow(rowIndex)}></button>
                        </td>
                    </tr>
                ))}
                </tbody>
            </table>
            <button onClick={addRow}> </button>
            <button onClick={handleSaveAllPDFs}>  PDF</button>
            <button onClick={() => setShowTable(false)}></button>
        </div>
    );
}

export default FioTable;
